@import url('https://fonts.googleapis.com/css2?family=Fredoka+One&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Baloo+2:wght@800&display=swap');



.App {
  text-align: center;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background-color: #3f216a;
  color: white;
  font-family: 'Fredoka One', sans-serif;
  font-size:larger;
  font-weight: bolder;
}

.board {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(3, 1fr);
  gap: 10px;
  border: 8px solid #8965db; 
  border-radius: 15px; 
  overflow: hidden; 
  background-color: #8965db;;
}

.box {
  width: 100px;
  height: 100px;
  background-color: #331957; 
  border: 2px solid #8965db;;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 50px;
  font-weight: bold;
  transition: background-color 0.3s ease, transform 0.2s ease;
  border-radius: 15px;
}

.box:hover {
  background-color: #53408f;
  cursor: pointer;
}

.box.X {
  color: #ff091a; 
  font-size: 80px; 
  font-weight: 1000; 
  font-family: 'Fredoka One', sans-serif; 
}

.box.O {
  color: #ffd700; 
  font-size: 80px;
  font-weight: 1000; 
  font-family: 'Fredoka One', sans-serif; 
}

.game-info {
  margin-top: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;
}

button {
  padding: 12px 24px;
  font-size: 22px;
  border-radius: 12px;
  border: none;
  background-color: #ff2d3b;
  color: white;
  cursor: pointer;
  transition: background-color 0.3s ease, transform 0.2s ease;
  margin-top: 20px; 
  font-family: 'Fredoka One', sans-serif; 
}

.status {
  color: #ffd700;
  font-size: 22px;
  font-weight: bold;
  background-color: #331957;
  padding: 20px;
  border-radius: 16px;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.2);
  margin-top: 20px; 
}

button:hover {
  background-color: #ff3045;
  transform: scale(1.05); 
}

.winner-message {
  color: #ffd700;
  font-size: 24px;
  font-weight: bold;
}

.draw-message {
  color: #ffd700;
  font-size: 24px;
  font-weight: bold;
}
